function isPrintVersion() {
  const isPrint = window.location.pathname.indexOf('/print') >= 0;
  return isPrint;
}

function isStartProtocol() {
  const isPrint = window.location.pathname.indexOf('/startProtocol') >= 0;
  return isPrint;
}

export default {
  isPrintVersion,
  isStartProtocol,
};



